@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
--Dark-brown: hsl(25, 47%, 15%);
--Medium-brown: hsl(28, 10%, 53%);
--Cream: hsl(27, 66%, 92%);
--Very-pale-orange: hsl(33, 100%, 98%);
--Soft-red: hsl(10, 79%, 65%);
--Cyan: hsl(186, 34%, 60%)
}

body{
    background-color: hsl(27, 66%, 92%);
    background-color: var(--Cream);
    font-size: 18px;
    font-family: 'DM Sans', sans-serif;
    box-sizing: border-box;
}
.Balance_balanceContainer__37yA4{
    background-color: var(--Soft-red);
    color: var(--Very-pale-orange);
    width: 90%;
    margin: 0 auto;
    margin-top: 5em;
    display: flex;
    justify-content: space-between;
    border-radius: 10px;
}

.Balance_balanceBlock__1-zrF, .Balance_logo__1O6w3{
    margin: 1.5em;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.Balance_balanceBlock__1-zrF {
    display: flex;
    flex-direction: column;
    grid-gap: 5px;
    gap: 5px;
}
.Balance_balanceHeading__1XQZw{
    font-size: 15px;
    font-weight: 400;
}
.Balance_balanceAmount__R2XFz{
    font-size: 1.5em;
    font-weight: 700;
}

@media only screen and (min-width: 600px){
    .Balance_balanceContainer__37yA4{
        width: 50%;
    }
}

@media only screen and (min-width: 1200px){
    .Balance_balanceContainer__37yA4{
        width: 30%;
    }
}
.SpendingChart_chartContainer__2RaK_{
    background-color: var(--Very-pale-orange);
    width: 90%;
    margin: 0 auto;
    margin-top: 1em;
    border-radius: 10px;
    min-height: 30vh;
    margin-bottom: 2em;
}


.SpendingChart_chartHeading__1eNe6{
    padding: 1.5rem 1rem 1.5rem 1rem;
    font-size: 1.2em;
    font-weight: 700;
    color: var(--Dark-brown);
}

.SpendingChart_barContainer__3JslW{
    display: flex;
    min-height: 10vh;
    justify-content: space-between;
    padding: 1.5rem 1rem 1.5rem 1rem;
    text-align: center; 
}

hr{
    padding: 0;
    margin: 0;
    margin: 0 1em 0 1em;
    border: none;
    border-bottom: 2px solid var(--Cream);
}

@media only screen and (min-width: 600px){
    .SpendingChart_chartContainer__2RaK_{
        width: 50%;
    }
}

@media only screen and (min-width: 1200px){
    .SpendingChart_chartContainer__2RaK_{
        width: 30%;
    }
}
.Chart_barBlock__pBXp4{
    position: relative;
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    justify-content: flex-end;
    text-align: center;
    height: 25vh;
}

.Chart_amount__112pt{
    font-size: 15px;
    display: none;
    position: absolute;
    background-color: var(--Dark-brown);
    top: 0;
    top: 0%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
    margin: 0 auto;
    min-width: 3em;
    color: white;
    padding: 0.5em;
    border-radius: 5px;
}
.Chart_bar__1CPqN{
    padding-top: 2em;
    background-color: var(--Soft-red);
    border-radius: 3px;
    width: 1.6em;
    cursor: pointer;
    margin: 0 auto;
}

.Chart_activeBar__UCnsX{
    background-color: var(--Cyan);
}

.Chart_barBlock__pBXp4:hover .Chart_amount__112pt{
    display: block;
}
.Chart_day__rigZU{
    font-size: 12px;
    color: var(--Medium-brown);
}

@media only screen and (min-width: 1200px){
    .Chart_bar__1CPqN{
        width: 2.3em;
    }
    
}
.TotalExpense_totalExpenseContainer__2LzXr{
    padding: 1.5rem 1rem 1.5rem 1rem;
    display: flex;
    justify-content: space-between;
}

.TotalExpense_expenseInfoBlock__2OxRS{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.TotalExpense_blockHeading__1qdwQ{
    font-size: 15px;
    color: var(--Medium-brown);
}

.TotalExpense_expenseAmount__2BkPX{
    font-size: 2em;
    font-weight: 700;
    color: var(--Dark-brown);
}

.TotalExpense_expenseStats__22aGb{
    font-weight: 700;
    color: var(--Dark-brown);
    text-align: right;
}
